body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

nav {
    width: 100%;
    height: auto;
    font-family: 'Muli', sans-serif;
    font-size: 26px;
    background-color: black;
    color: white;
    position: fixed;
    z-index: 99;
}

nav li {
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 30px;
}

a {
    color: white;
    margin-right: 220px;
}

a:hover {
    text-decoration: none;
    color: white;
}

ul {
    display: flex;
    margin-bottom: 0px;
    padding-top: 15px;
}

#rightWrong {
    margin-right: 220px;
}

.header {
    background-color: white;
    border: 2px solid black;
	width: 100%;
}

h1 {
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
}

h2 {
	text-align: center;
	font-size: 30px;
	padding-bottom: 40px;
}

h3 {
	text-align: center;
	font-size: 20px;
	padding-bottom: 40px;
}
.card {
    width: 200px;
    height: 200px;
    margin: 1rem;
    border-radius: 2px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }

  .card:hover {
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
  }

  img {
    width: 200px;
    height: 200px;
    overflow: hidden;
    cursor: pointer;
  }

  img:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: .1s;
    transition: .1s;
  }
.footer {
    background-color: black;
    width: 100%;
	position: relative;
	height: 50px;
	text-align: right;
}

.footer a{
	color: white;
	font-size: 20px;
}

